import "./App.css";
import JsonInput from "./components/JsonInput";
import OutputJson from "./components/OutputJson";
import { useState } from "react";
import { Grid, Typography, Divider, IconButton } from "@material-ui/core";
import GitHubIcon from "@material-ui/icons/GitHub";
import axios from "axios";

const App = () => {
  const [jsonFromInput, setJsonFromInput] = useState("");
  const [currentJsonFromInput, setCurrentJsonFromInput] = useState("");
  const [currentSpaceOption, setCurrentSapceOption] = useState(4);
  const [dataTypes, setDataTypes] = useState(true);
  const [elementCount, setElementCount] = useState(true);
  const [theme, setTheme] = useState("rjv-default");

  const onInputHandler = async (event) => {
    event.preventDefault();
    try{
      const response = await axios.get("https://cors-anywhere.herokuapp.com/"+currentJsonFromInput);
      if(response) return setJsonFromInput(response.data);
    }
    catch{
      let jsonParsed = JSON.parse(currentJsonFromInput);

      setJsonFromInput(jsonParsed);
    }
  };

  const processJson = (json, spaces) => {
    setJsonFromInput();
  };

  const onChangeHandler = (event) => {
    const inputVale = event.target.value;

    setCurrentJsonFromInput(inputVale);

    console.log(inputVale);
  };

  const onOptionInputHandler = (event, newValue) => {
    setCurrentSapceOption(newValue);
  };

  const clearInputAndOutput = () => {
    setCurrentJsonFromInput("");
    setJsonFromInput("");
  }

  const onDataTypesChecked = () => {
    setDataTypes((prev) => !prev);
  };

  const onElementCountChecked = () => {
    setElementCount((prev) => !prev);
  };

  const themeHanlder = (event) => {
    const themeValue = event.target.value;
    console.log("rerqw");
    setTheme(themeValue);
  };

  return (
    <>
      <Grid container spacing={30}>
        <Grid item xs={6} lg={11}>
          <Typography style={{ "text-align": "center" }} variant="h4">
            Simple JSON Viewer
          </Typography>
        </Grid>
        <Grid item xs={6} lg={1}>
          <IconButton
            rel="noreferrer"
            target="_blank"
            href="https://github.com/viktor111"
          >
            <GitHubIcon />
          </IconButton>
        </Grid>
        <Grid item xs={12} lg={12}>
          <Divider />
        </Grid>
      </Grid>
      <Grid container spacing={10}>
        <Grid item xs={12} lg={6}>
          <JsonInput
            inputHandler={onInputHandler}
            changeHandler={onChangeHandler}
            processJson={processJson}
            optionInput={onOptionInputHandler}
            spaces={currentSpaceOption}
            toggleCheckedDataTypes={onDataTypesChecked}
            toggleElementCount={onElementCountChecked}
            showDataTypes={dataTypes}
            showElementCount={elementCount}
            theme={theme}
            themeHanlder={themeHanlder}
            json={currentJsonFromInput}
            clear={clearInputAndOutput}
          />
        </Grid>
        <Grid item xs={12} lg={6}>
          <OutputJson
            json={jsonFromInput}
            spaces={currentSpaceOption}
            processJson={processJson}
            showDataTypes={dataTypes}
            showElementCount={elementCount}
            theme={theme}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default App;
