import ReactJson from 'react-json-view';
import { Paper } from "@material-ui/core";


const OutputJson = (props) => {

    const renderJson= () => {

        if(props.json === "") return(
            <Paper elevation={3} style={{ height:840, margin: 10, maxHeight: 840, overflow: 'auto'}}>
                
            </Paper>
        )

        return(
            <Paper elevation={3} style={{ height:840, margin: 10, maxHeight: 840, overflow: 'auto'}}>
                <ReactJson theme={props.theme} displayObjectSize={props.showElementCount} displayDataTypes={props.showDataTypes} indentWidth={parseInt(props.spaces)} src={props.json}/>
            </Paper>
        )
    }

  return (
    <>
        {renderJson()}
    </>
  );
}

export default OutputJson;
