import { Paper, Button, TextField, Grid } from "@material-ui/core";
import Options from "./Options";

const JsonInput = (props) => {
  return (
    <>
      <form onSubmit={props.inputHandler}>
        <Paper style={{ padding: 30, margin: 10 }} elevation={3}>
          <Grid container>
            <Grid item xs={12} lg={12}>
              <TextField
                value={props.json}
                multiline
                rowsMax={20}
                onChange={props.changeHandler}
                label="Link or JSON"
                variant="outlined"
                fullWidth
              />
            </Grid>
            <Grid item xs={6} lg={6}>
              <Button type="input">View</Button>
            </Grid>
            <Grid item xs={6} lg={6}>
              <Button onClick={props.clear}>Clear</Button>
            </Grid>
          </Grid>
        </Paper>
      </form>

      <Options
        style={{ "text-align": "center" }}
        toggleCheckedDataTypes={props.toggleCheckedDataTypes}
        toggleElementCount={props.toggleElementCount}
        spaces={props.spaces}
        optionInput={props.optionInput}
        showDataTypes={props.showDataTypes}
        showElementCount={props.showElementCount}
        themeChange={props.theme}
        themeHanlder={props.themeHanlder}
      />
    </>
  );
};

export default JsonInput;
