import {
  Paper,
  Slider,
  Typography,
  Grid,
  Switch,
  FormControlLabel,
  InputLabel,
  Select,
  MenuItem,
} from "@material-ui/core";
import { withStyles } from '@material-ui/core/styles';

const Options = (props) => {
    const IOSSwitch = withStyles((theme) => ({
        root: {
          width: 42,
          height: 26,
          padding: 5,
          margin: theme.spacing(1),
        },
        switchBase: {
          padding: 1,
          '&$checked': {
            transform: 'translateX(16px)',
            color: theme.palette.common.white,
            '& + $track': {
              backgroundColor: '#52d869',
              opacity: 1,
              border: 'none',
            },
          },
          '&$focusVisible $thumb': {
            color: '#52d869',
            border: '6px solid #fff',
          },
        },
        thumb: {
          width: 24,
          height: 24,
        },
        track: {
          borderRadius: 26 / 2,
          border: `1px solid ${theme.palette.grey[400]}`,
          backgroundColor: theme.palette.grey[50],
          opacity: 1,
          transition: theme.transitions.create(['background-color', 'border']),
        },
        checked: {},
        focusVisible: {},
      }))(({ classes, ...props }) => {
        return (
          <Switch
            focusVisibleClassName={classes.focusVisible}
            disableRipple
            classes={{
              root: classes.root,
              switchBase: classes.switchBase,
              thumb: classes.thumb,
              track: classes.track,
              checked: classes.checked,
            }}
            {...props}
          />
        );
      });
 
  return (
    <Paper style={{ padding: 30, margin: 10 }} elevation={3}>
      <Grid container>
        <Grid item xs={12} lg={12}>
          <Typography id="discrete-slider" gutterBottom>
            Tab Spaces
          </Typography>
          <Slider
            onChange={props.optionInput}
            aria-labelledby="discrete-slider"
            valueLabelDisplay="auto"
            defaultValue={4}
            step={2}
            min={0}
            marks
            max={12}
          />
        </Grid>
        <Grid item xs={6} lg={6}>
          <FormControlLabel
            control={
              <IOSSwitch
                checked={props.showDataTypes}
                onChange={props.toggleCheckedDataTypes}
              />
            }
            label="Show data types"
          />
        </Grid>
        <Grid item xs={6} lg={6}>
          <FormControlLabel
            control={
              <IOSSwitch
                checked={props.showElementCount}
                onChange={props.toggleElementCount}
              />
            }
            label="Show element count"
          />
        </Grid>
        <Grid item xs={12} lg={12}>
        <InputLabel id="demo-simple-select-label">Theme</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={props.themeChange}
          onChange={props.themeHanlder}
        >
          <MenuItem value={"rjv-default"}>Default</MenuItem>
          <MenuItem value={"ocean"}>Ocean</MenuItem>
          <MenuItem value={"solarized"}>Solarized</MenuItem>
          <MenuItem value={"apathy"}>Apathy</MenuItem>
          <MenuItem value={"isotope"}>High Contrat</MenuItem>
        </Select>
        </Grid>
      </Grid>
    </Paper>
  );
};

export default Options;
